var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-principal color-acento-botones nt"},[_c('div',{staticClass:"titulo-principal__numero"},[_c('span',[_vm._v("5")])]),_c('h1',[_vm._v("Laminación y "),_c('em',[_vm._v("Directed Energy Deposition")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('p',{staticClass:"mb-4"},[_vm._v("La deposición directa de energía (DED) es un proceso de impresión 3D más complejo, a menudo se usa para reparar piezas existentes o agregar material adicional. Los modelos se pueden hacer completamente desde cero utilizando esta técnica, pero su uso principal es para aplicaciones industriales, como la reparación de turbinas o hélices dañadas. Al igual que algunas técnicas de lecho de polvo, la deposición de energía directa utiliza una fuente de energía enfocada, como un láser o un haz de electrones, para derretir el material. Sin embargo, el material se funde mientras se deposita en la boquilla. En cierto modo, la tecnología está en el límite entre la extrusión de material y la fusión de lecho de polvo. Esta tecnología deposita material sobre la base o el componente que se está reparando a través de boquillas montadas en brazos multieje (generalmente 4 o 5). El material metálico suministrado a la boquilla se proporciona en forma de polvo o alambre (3Dnatives, 10 de septiembre de 2019). ")]),_c('p',{staticClass:"mb-4"},[_vm._v("Cuando se habla de la deposición de energía dirigida, se utilizan términos como revestimiento láser 3D, conformación de red diseñada con láser, fabricación de luz directa y deposición de metal. Este es un proceso de impresión más avanzado que se utiliza para agregar material adicional a los componentes existentes o reparar daños. Las máquinas DED depositan el material fundido a través de una boquilla móvil unida a un brazo de múltiples puntas. El brazo posiciona la boquilla en diversas direcciones para derretir diferentes áreas de la superficie objetivo. Una vez que el material se endurece, el proceso es similar a la extrusión de material sin eje fijo. Estas máquinas permiten fundir materiales en cualquier ángulo debido a su material. Alternativamente, los rayos de electrones o láser pueden derretir el material.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema5/img-1.png")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-botones",attrs:{"id":"t_5_1"}},[_c('h2',[_vm._v("5.1 Usos ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-botones",attrs:{"id":"t_5_2"}},[_c('h2',[_vm._v("5.2 Ventajas y desventajas ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mb-5"},[_vm._v("A continuación, las ventajas y desventajas del uso de "),_c('em',[_vm._v("Directed Energy Deposition")])])}]

